#tiles {
  height: 100vh;
  width: auto;
  z-index: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-template-rows: repeat(var(--rows), 1fr);
  padding: 1px 2px 2px 1px;
  display: grid;
  position: relative;
  left: 0;
}

.tile {
  cursor: pointer;
  opacity: .95;
  position: relative;
}

.tile:before {
  background-color: var(--primary-bg-color);
  content: "";
  position: absolute;
  inset: 1.5px;
}

.tile:hover {
  opacity: .5;
}

main {
  background: linear-gradient(to right, #9477f5, #f58a78, #9477f5) 0 0 / 200%;
  animation: 7s linear infinite background-pan;
  top: 0;
}

@keyframes background-pan {
  from {
    background-position: 0%;
  }

  to {
    background-position: -200%;
  }
}

